// import _ from 'lodash'
import React from 'react';
import Slider from 'react-slick';

// import './map.css';

class SimpleSlider extends React.PureComponent {
  render() {
    const settings = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
    };
    return (
      <Slider {...settings} className="full-width">
        <ul className="slides">
          <li>
            <img alt="" src="http://via.placeholder.com/1600x450" />
            <div className="caption contain">
              <h2><span>an awesome website</span></h2>
              <h2><span>html theme</span></h2>
              <p>
                Lorem Ipsum is simply dummy text of the printing and typesetting industry.
              </p>
              <button className="btn">Read More</button>
            </div>
          </li>
        </ul>
      </Slider>
    );
  }
}

export default SimpleSlider;



