import React from 'react';
import Link from '../../Atom/Link';
import $ from 'jquery';
// import { Grid, Cell } from 'react-mdl';
// import s from './Head.css';
// import Card from '../../Atom/Card';
// import classnames from 'classnames';
// import Button from 'material-components-web';
// import Nav from '../../Layout/Navigation';
// import NavMobile from '../../Molecule/NavMobile';

// import '@material/button/dist/mdc.button.css';

class Head extends React.Component {
  componentDidMount() {
    window.componentHandler.upgradeElement(this.root);
  }

  componentWillUnmount() {
    window.componentHandler.downgradeElements(this.root);
  }

  searchClick(e) {
    e.preventDefault();
    $(this).toggleClass('current');
    $('.search-form').toggleClass('visible');
  }

  render() {
    return (
      <header id="home" ref={node => (this.root = node)}>

        <section className="top-nav hidden-xs">
          <div className="container">
            <div className="row">
              <div className="col-md-6">
                <div className="top-left">

                  <ul>
                    <li>
                      <a href="#">
                        <i className="fa fa-facebook" aria-hidden="true" />
                      </a>
                    </li>
                    <li>
                      <a href="#">
                        <i className="fa fa-twitter" aria-hidden="true" />
                      </a>
                    </li>
                    <li>
                      <a href="#">
                        <i className="fa fa-linkedin" aria-hidden="true" />
                      </a>
                    </li>
                    <li>
                      <a href="#">
                        <i className="fa fa-vk" aria-hidden="true" />
                      </a>
                    </li>
                    <li>
                      <a href="#">
                        <i className="fa fa-instagram" aria-hidden="true" />
                      </a>
                    </li>
                  </ul>

                </div>
              </div>

              <div className="col-md-6">
                <div className="top-right">
                  <p>Location:<span>Main Street 2020, City 3000</span></p>
                </div>
              </div>

            </div>
          </div>
        </section>

        <div id="main-nav">

          <nav className="navbar">
            <div className="container">

              <div className="navbar-header">
                <a href="index.html" className="navbar-brand">MyBiz</a>
                <button
                  type="button"
                  className="navbar-toggle collapsed"
                  data-toggle="collapse"
                  data-target="#ftheme"
                >
                  <span className="sr-only">Toggle</span>
                  <span className="icon-bar" />
                  <span className="icon-bar" />
                  <span className="icon-bar" />
                </button>
              </div>

              <div className="navbar-collapse collapse" id="ftheme">

                <ul className="nav navbar-nav navbar-right">
                  <li><Link to="/">home</Link></li>
                  <li><Link to="/about">about</Link></li>
                  <li><Link to="/about">services</Link></li>
                  <li className="hidden-sm hidden-xs">
                    <a href="#" id="ss" className="current">
                      <i className="fa fa-search" aria-hidden="true" />
                    </a>
                  </li>

                </ul>

              </div>

              <div className="search-form">
                <form>
                  <input type="text" id="s" size="40" placeholder="Search..." />
                </form>
              </div>

            </div>
          </nav>
        </div>

      </header>
    );
  }
}

export default Head;



