import React, { PropTypes } from 'react';
import Layout from '../../components/Layout';
import Service from '../../components/Organism/Service';
import Portfolio from '../../components/Organism/Portfolio';
import Slider from '../../components/Atom/Slider';
import About from '../../components/Organism/About';
import Map from '../../components/Atom/Map';
import s from './styles.css';
import { title, html } from './index.md';

import { Button } from 'react-bootstrap';

class HomePage extends React.Component {
  static propTypes = {
    articles: PropTypes.array.isRequired,
  };

  componentDidMount() {
    document.title = title;
  }

  render() {
    return (
      <Layout className={s.content}>
        <Slider />
        <About />
        <div dangerouslySetInnerHTML={{ __html: html }} />
        <h4>Articles</h4>
        <ul />
        <p>

          <Button bsStyle="primary" bsSize="large" active>Primary</Button>

          <br /><br />
        </p>
        <Service />
        <Portfolio />
        <Map />

      </Layout>
    );
  }
}

export default HomePage;



